<template>
  <div id="adjuntos">
    <v-card
      class="mx-auto"
      dense
      v-for="item in dataAdjuntos" :key="item.tipo"
    >
        <v-card
          v-if="(item.tipo == 1) || (item.tipo == 2 && cargardefinitivo == 1) "
        >
            <v-card-title>{{ `${item.tipo == 1 ? 'Preliminar' : 'Definitivo'}` }}</v-card-title>
            <v-card-text>
                <v-chip v-if="item.estado == 'A'"
                    class="ma-2"
                    @click="openInNewTab(item.file_path, item.file_type)"
                    color="green darken-2"
                    text-color="white"
                    style="margin: 5px"
                >
                    {{item.file_name.substring(0, 50)}}
                </v-chip>
                <v-btn text v-else>
                  <AddFilesToStorage
                    :item="item"
                    placeholder="Cargar Archivo"
                    dense
                    hide-details
                    prepend-icon
                    @remove-file="fileReset++"
                    :key="fileReset"
                    @file-changed="fileReset++"
                  />
                </v-btn>
                <br>
                <br>
                <h3><p><span class="font-weight-bold">Estado: </span><span :class="[{ColorVBFalse:(item.estado === 'R')}, {ColorVBTrue:(item.estado !== 'A')}]">{{ item.descripcionestado }}</span></p></h3>
                <h3><p><span class="font-weight-bold">Observación: </span><span>{{ item.observacion }}</span></p></h3>
            </v-card-text>
            <v-card-actions>
            <v-btn
                class="mx-2"
                fab
                dark
                color="primary"
                @click="guardarDocumentos(item)"
                v-if="item.estado != 'A'"
            >
                <v-icon dark>
                    save
                </v-icon>
            </v-btn>
            </v-card-actions>
        </v-card>
        <br>
    </v-card>
  </div>
</template>
<script>
import AddFilesToStorage from '@/components/shared/AddFilesToStorage'
import { adminApp } from '@/services/api'
import FileExchange from '../../../services/FileExchange'
import { b64toBlobUrl } from '../../../services/utilities'
export default {
  props: ['idpropconv'],
  components: { AddFilesToStorage },
  data () {
    return {
      dataAdjuntos: [],
      resetComponent: 0,
      fileReset: 0,
      cargardefinitivo: 0
    }
  },
  async mounted () {
    this.getData()
  },
  methods: {
    async getData () {
      this.dataAdjuntos = []
      const { data } = await adminApp.buscarMinutaContrato({
        idpropconv: this.idpropconv
      })
      this.dataAdjuntos = data.archivos
      if (this.dataAdjuntos.length > 0) {
        const element = this.dataAdjuntos[0]
        if (element.tipo === 1 && element.estado === 'A') {
          this.cargardefinitivo = 1
        }
      }
    },
    async openInNewTab (url, type) {
      try {
        const result = await FileExchange.getFileFromStorage(url)
        const fileData = result.data
        const urlBlob = b64toBlobUrl(fileData, type, '')
        var win = window.open(urlBlob, '_blank')
        win.focus()
      } catch (error) {
        console.log(error)
      }
    },
    async guardarDocumentos (item) {
      var dataadjuntos = []
      dataadjuntos.push({ propuestaconvocatoriaid: this.idpropconv, tipo: item.tipo, file_name: item.file_name, file_path: item.file_path, file_size: item.file_size, file_type: item.file_type })
      const { data } = await adminApp.guardardatosAdjuntosMinuta({
        dataadjuntos: dataadjuntos
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.mensaje, { icon: 'done', duration: 5000 })
          this.getData()
        } else {
          this.$toasted.error(data.mensaje, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
    }
  },
  computed: {
    adjuntos: {
      get () {
        return this.dataAdjuntos
      },
      set (val) {
        this.adjuntos = val
      }
    }
  },
  watch: {
    idpropconv (val) {
      this.getData()
    }
  }
}
</script>
<style>
  .anexosClass {
    min-width: 300px;
  }
  #adjuntos .v-label {
    font-size: .70rem;
  }
  #adjuntos .v-label .inFiles{
    font-size: .95rem;
  }
  .ColorVBTrue{
    color:blue;
    font-weight: bold;
  }
  .ColorVBFalse{
    color:red;
    font-weight: bold;
  }
</style>
